<template>
  <div style="width:100%; height:100%; display:block;background:#fff;">
    <mobile-header
      v-if="$route.query.loginFlag"
      :title="
        $route.query.loginFlag === 'TYPE-0'
          ? '장례식장 로그인'
          : '장례지도사 로그인'
      "
      left-icon="mdi-chevron-left"
      class="mb-0"
      right-icon="mdi-help-circle-outline"
      style="border-bottom: 0px"
      @close="move('/login')"
    />
    <div>
      <div class="login_logo">
        <img
          width="136px"
          height="37px"
          src="@/assets/images/common/logo_brown.png"
          alt="logo image"
          class="ml-6"
          style="margin-top:76px"
        />
      </div>
      <div id="m_login" class="px-4">
        <div>
          <div>
            <v-form ref="form" v-model="valid" lazy-validation>
              <span class="input-title">아이디</span>
              <v-text-field
                v-model="member.id"
                :rules="rules.id"
                color="main"
                placeholder="아이디를 입력해주세요"
                required
              />

              <span class="input-title">비밀번호</span>
              <v-text-field
                v-model="member.memberPw"
                :rules="rules.password"
                :type="show1 ? 'text' : 'password'"
                color="main"
                placeholder="비밀번호를 입력해주세요"
                required
                :append-icon="show1 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
                @click:append="show1 = !show1"
              />

              <button
                :disabled="!valid"
                class="btn_login"
                type="submit"
                @click="submit"
              >
                로그인
              </button>
            </v-form>
            <button class="btn_login join" @click="join">회원가입</button>
          </div>

          <div class="search">
            <a @click="move('/searchId')">아이디 찾기</a>
            <a @click="move('/searchPw')">비밀번호 찾기</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "@/assets/js/jquery-1.9.1.min.js";
import "@/assets/js/ux_common.js";
import MobileHeader from "../../components/common/MobileHeader.vue";
export default {
  name: "LoginDo",
  components: { MobileHeader },
  data() {
    return {
      loginFlag: this.$route.query.loginFlag
        ? this.$route.query.loginFlag
        : "TYPE-1",
      member: {
        id: "",
        memberPw: ""
      },
      show1: false,
      valid: true,
      rules: {
        id: [val => (val || "").length > 0 || "아이디를 입력하여 주십시오"],
        password: [
          val => (val || "").length > 0 || "비밀번호를 입력하여 주십시오"
        ]
      }
    };
  },
  methods: {
    move(path) {
      this.$router.push(path);
    },
    join() {
      if (this.loginFlag === "TYPE-0") {
        this.$router.push({ path: "/joinCorp" });
      } else {
        this.$router.push({ path: "/joinSolo" });
      }
    },
    submit() {
      const parameter = {
        member: this.member,
        loginFlag: this.loginFlag
      };
      this.$store.dispatch("REQUEST_LOGIN", parameter);
    }
  }
};
</script>
